.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}





* {
  margin: 0;
  padding: 0;
  color: #585858;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Yu Gothic", YuGothic, "ヒラギノ角ゴ ProN W3", Hiragino Kaku Gothic ProN, Arial, "メイリオ", Meiryo, sans-serif;
}

body {
  background: #F2F2F2;
}

a {
  text-decoration: none;
  cursor: pointer;
}

header {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 70px;
  background: #2D2D2D;
  border-top: 4px solid #0489B1;
}

#header-left {
  padding-left: 2%;
  width: 50%;
  line-height: 70px;
  font-size: 195%;
  font-weight: bold;
  letter-spacing: -.7px;
}

#header-left span {
  font-family: 'Architects Daughter';
  -webkit-transition: .7s;
  -o-transition: .7s;
  transition: .7s
}

#header-left a:hover > span {
  color: #6E6E6E;
}

#bytakaohayashi {
  font-size: 50%;
}

#menu-button-wrap {
  display: none;
  width: 25%;
  height: 100%;
  text-align: right;
  font-size: 60%;
}

#menu-button-wrap span {
  height: 100%;
  line-height: 70px;
}

#menu-button-wrap i {
  color: #585858;
}

#header-right {
  display: -ms-flexbox;
  display: flex;
  padding-right: 2%;
  width: 50%;
}

#search {
  padding-top: 20px;
  width: 70%;
  text-align: right;
}

#search input[type='text'] {
  padding-left: 3px;
  width: 55%;
  height: 30px;
  /*background: url('/assets/images/search-3-16.png') no-repeat white 2% center;*/
  border: none;
}

#icons {
  padding-top: 15px;
  width: 30%;
  text-align: right;
}

#menu {
  display: none;
  position: fixed;
  top: 114px;
  padding: 0 21.79px;
  width: 100%;
  background: #2D2D2D;
  z-index: 1;     /* これを指定しないと、aside が #menu の上に表示されてしまう */
}

#menu div {
  margin-bottom: 15px;
}

main {
  width: 100%;
}

#main-wrapper {
  display: -ms-flexbox;
  display: flex;
  margin: 20px auto;
  width: 90%;
  min-height: 500px;
}

#article-list {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-right: 1%;
  padding: 15px;
  width: 70%;
  background: white;
}

.article-wrapper {
  margin-bottom: 100px;
}

.created-at {
  margin-bottom: 5px;
  font-size: 85%;
  color: silver;
}

.article-wrapper h2 {
  margin-bottom: 5px;
}

.category {
  margin-bottom: 25px;
  font-size: 70%;
}

.category a {
  padding: 3px;
  background: #0489B1;
  border-radius: 3px;
  color: white;
}

.content {
  font-size: 95%;
}

.article-row {
  line-height: 25px;
}

.content h3 {
  margin-bottom: 21px;
  padding-left: 5px;
  border-left: 5px solid #0489B1;
  border-bottom: 3px solid #E6E6E6;
  font-size: 130%;
}

.content a, .content a span {
  color: #0489B1 !important;
}

.nl {
  height: 21px;
}

.filename {
  /* 他の要素の margin に合わせて 21px にすると、ファイル名の上部がテキストであった場合に直感的な見た目のバランスが悪いため、検証の結果を踏まえ 14px とする */
  /* .code の要素が上部にあると、.code の margin-bottom と相殺される（値が大きい .code の margin-bottom が優先され、要素間は 21px となる） */
  margin-top: 14px;

  margin-bottom: -21px;
  word-wrap: break-word;
}

.filename span {
  color: #848484;
}

.code {
  margin: 21px auto;
  line-height: 21px;
  border: 1px dotted #D8D8D8;
}

code span, plaintext span {
  font-family: 'Courier New', Consolas, Courier, Monaco, monospace !important;
}

plaintext span {
  color: #585858 !important;
}

/* SyntaxHighlighter において、wrapLines={true} とすると最後に空白行が発生してしまうため、以下で対処 */
code > span:last-child, plaintext > span:last-child {
  display: none !important;
}

.highlight {
  background: #DBFFDB;
}

#page-link {
  text-align: center;
}

#page-link a {
  color: #0489B1;
  text-decoration: underline;
}

aside {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-left: 1%;
  padding: 20px 15px;
  width: 28%;
  background: white;
}

.sidebar-heading {
  position: relative;
  padding-left: 25px;
  line-height: 20px;
}

.sidebar-heading:before, .sidebar-heading:after {
  content: '';
  position: absolute;
  width: 4px;
  background: #0489B1;
}

.sidebar-heading:before {
  top: 8px;
  left: 2px;
  height: 10px;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.sidebar-heading:after {
  top: 0px;
  left: 10px;
  height: 18px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

#about {
  margin: 7px auto 50px auto;
  font-size: 85%;
}

#category-list {
  margin: 7px auto 50px auto;
  line-height: 20px;
  font-size: 80%;
}

#category-list a {
  text-decoration: underline;
  color: #0489B1;
}

#contact {
  margin: 7px auto 0 auto;
  font-size: 16px;     /* iOS では、input 要素のフォントサイズが 16px を下回ると、フォーカス時にズームされてしまう */
}

#contact div:nth-child(-n+4) {
  padding-bottom: 10px;
  width: 100%;
}

#contact input[type='text'], #contact input[type='email'], #contact textarea {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 5px;
  width: 100%;
  border: 1px solid #D8D8D8;
  -webkit-box-shadow: none;
          box-shadow: none;     /* materialui を導入すると、firefox のみ赤枠が初期表示されてしまう症状への対処 */
}

#contact input[type='text'], #contact input[type='email'] {
  height: 30px;
}

#contact textarea {
  height: 200px;
}

#contact-submit-btn {
  width: 15%;
  height: 30px;
  background: white;
  border: 1px solid #D8D8D8;
  cursor: pointer;
}

#sending-img {
  display: none;
  vertical-align: middle;
}

@media screen and (max-width: 1000px) {
  header {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    position: fixed;
    top: 0;
    height: 110px;
    z-index: 1;     /* これを指定しないと、aside が header の上に表示されてしまう */
  }
  #header-left {
    display: -ms-flexbox;
    display: flex;
    padding-left: 0px;
    width: 100%;
    height: 70px;
  }
  #header-left a {
    padding-left: 21.79px;
    width: 75%;
  }
  #menu-button-wrap {
    display: block;
  }
  #header-right {
    padding-right: 0;
    width: 100%;
    height: 40px;
  }
  #search {
    padding: 0 21.79px;
    width: 100%;
    text-align: center;
  }
  #search input[type='text'] {
    width: 100%;
    height: 25px;
    -webkit-appearance: none;     /* iOS にて、影がつかないようにする */
    border-radius: 0;     /* iOS にて、角が丸くならないようにする */
    font-size: 16px;     /* iOS では、input 要素のフォントサイズが 16px を下回ると、フォーカス時にズームされてしまう */
  }
  #icons {
    display: none;
  }
  main {
    /*
    子要素の #main-wrapper においても margin-top を使用しているため、main に margin-top を適用することにより、値の小さい子要素の margin が相殺されてしまう
    よって、#main-wrapper に適用している margin-top の値 20px と header の高さ 114px を合算し 134px とする
    */
    margin-top: 134px;
  }
  #main-wrapper {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  #article-list {
    margin-right: 0px;
    width: 100%;
  }
  aside {
    margin-top: 20px;
    margin-left: 0;
    width: 100%;
  }
  #contact input[type='text'], #contact input[type='email'], #contact textarea {
    -webkit-appearance: none;     /* iOS にて、影がつかないようにする */
    border-radius: 0;     /* iOS にて、角が丸くならないようにする */
    font-size: 16px;     /* iOS では、input 要素のフォントサイズが 16px を下回ると、フォーカス時にズームされてしまう */
  }
}





/* フッタの表示をコンテンツ量が少ない時は最下部に固定、多い時は成り行きにする */

html {
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 65px;     /* #wrapper の margin-bottom（20px）と footer の height（45px）を合計 */
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 45px;
  text-align: center;
  line-height: 45px;
  background: #2D2D2D;
  font-size: 80%;
}


